@use "../mixins";
@use "../variables";

.input {
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }
}

.input__label {
  @include mixins.text--normal(14px, variables.$text-color-secondary);
  text-transform: uppercase;
  display: block;
}

.input__wrapper {
  position: relative;
}

.input__edit {
  @include mixins.text--bold(18px, variables.$text-color-primary);
  border: none;
  background: #f2f2f2;
  padding: 16px;
  width: 100%;
}

.input__edit--icon {
  padding: 16px 74px 16px 16px;
}

.input__icon-wrapper {
  width: 58px;
  height: 58px;
  position: absolute;
  right: 0;
  top: 0;
  background-color: #e6e6e6;
  display: flex;
  justify-content: center;
  align-items: center;
}

.input__icon-wrapper::after {
  display: block;
  content: "";
}

.input__icon-wrapper--phone::after {
  mask-image: url("../img/sprite.svg#phone");
  width: 18px;
  height: 18px;
  background-color: #010101;
}

.input__icon-wrapper--email::after {
  mask-image: url("../img/sprite.svg#email");
  width: 16px;
  height: 12px;
  background-color: #000000;
}

.input__edit::placeholder {
  @include mixins.text--bold(18px, variables.$text-color-primary-opacity);
}
