@use "../mixins";
@use "../variables";

.radio {
  position: relative;
  display: block;
  min-height: 46px;
  cursor: pointer;
}

.radio__input {
  position: absolute;
  outline: none;
  clip: rect(0, 0, 0, 0);
}

.radio__name {
  @include mixins.text--bold(18px, variables.$text-color-primary);
  padding: 10px 10px 10px 62px;
  display: block;
  position: relative;
}

.radio__name::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 50%;
}

.radio__input:checked ~ .radio__name::after {
  position: absolute;
  content: "";
  left: 16px;
  top: 16px;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
}
